export default {
  data () {
    return {
      src: '',
      pageShow: false
    }
  },

  onLoad () {
    this.getQRcode()
  },

  methods: {
    getQRcode () {
      this.$http.getDistributionORode().then(res => {
        if (res.status === 200) {
          this.src = res.data.qrcodeurl

          setTimeout(() => {
            this.pageShow = true
          }, 300)
        }
      })
    },

    preview () {
      uni.previewImage({
        current: 0,
        urls: [this.src]
      })
    }
  }
}
